.poster {
  width: 150px;
  height: 150px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  p {
    position: relative;
    top: 5px;
    left: 5px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.form-inline .form-control {
  display: inline-block;
  width: auto;
  vertical-align: middle;
  margin-right: 0.5rem;
}

@media (min-width: 576px) {
  .poster {
    width: 600px;
    height: 600px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    h4 {
      position: relative;
      top: 10px;
      left: 10px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  .form-inline .form-control {
    display: inline-block;
    width: auto;
    vertical-align: middle;
  }
}
